<template>

  <v-app>
  <v-container>
    <h2 v-html="page.description" class="center-text"></h2>
    <div>
      <p v-html="page.PARAGRAPH"></p>
      <p class="updatedAt">
      {{page.NAME}} - Updated: {{formatDate(page.DATETIME)}}; Author: {{page.AUTHOR}}
      </p>
    </div>
  </v-container>
</v-app>

</template>

<script>
import mixins from '@/mixins'

export default {
  data () {
    return {
      message: "Page"
    }
  },
  mixins: [mixins],
  computed: {
    pages: function() {
      return this.$store.getters.pages
    },
    page: function( ) {
      if ( !this.isNumeric(this.$route.params.id) ) {
        // return "String"
        return this.pages.filter( el => el.NAME.includes(this.$route.params.id) )[0]
      } else {
        // return "Number"
        return this.pages.filter( el => el.ID === parseInt(this.$route.params.id) )[0]
      }
    },
  },
  methods: {
  },
  created () {
    console.log(this.$route.params.id)
  }
}
</script>

<style scoped>
.updatedAt {
  font-size: .8em;
  text-align: right
}
</style>